import { getNewVersion } from '@/api/'
export default {
  data () {
    return {
      version: '1.0.0'
    }
  },
  beforeRouteEnter (to, from, next) {
    document.title = '关于我们'
    next()
  },
  created () {
    getNewVersion({type: 0}).then((res) => {
      if (!Array.isArray(res.result)) {
        this.version = res.result.data[0].version_num
      }
    })
  }
}



